.header {
  position: absolute;
  z-index: 5;
  top: 0;
  left: 0;
  width: 100%;
  border-bottom: 1px solid rgba(255, 255, 255, 0.4);
  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  &__nav-menu {

  }
  &__socials {
    margin-left: 55px;
    @media screen and (max-width: 450px) {
      margin-inline-start: 0px;
    }
  }
}
.logo-wrapper {

}
.logo {
  @media screen and (max-width: 450px) {
    & img {
      max-width: none;
      width: 50px;
    }
  }
}
.burger {
  display: none;
  width: 50px;
  height: 50px;
  border: 2px solid #f5f5f5;
  border-radius: 5px;
  margin-left: 50px;
  position: relative;
  z-index: 10;
  & span {
    position: absolute;
    top: 12px;
    left: 6px;
    height: 2px;
    width: 34px;
    background-color: #f5f5f5;
    transition: top, transform;
    transition-delay: 0s, 1s;
    transition-duration: .2s, .2s;
    &::before,
    &::after {
      content: '';
      position: absolute;
      height: 2px;
      width: 34px;
      background-color: #f5f5f5;
      transform-origin: center;
    }
    &::before {
      top: 10px;
    }
    &::after {
      top: 20px;
      transition: opacity, top;
      transition-delay: .2s, 0s;
      transition-duration: 0s, .2s;
    }
  }
  &--active span {
    transform-origin: center;
    top: 22px;
    transform: rotate(45deg);
    &::before {
      top: 0px;
    transform: rotate(-90deg);
    }
    &::after {
      top: 0px;
      opacity: 0;
    }
  }
  @media screen and (max-width: 1024px) {
    display: block;
    order: 2;
  }
  @media screen and (max-width: 450px) {
    margin-left: 25px;
  }
}
.nav-menu-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  & .fa-icon {
    color: #f5f5f5;
  }
}
.nav-menu {
  display: flex;
  justify-content: space-between;
  &__item {
    font-family: $myriad;
    font-size: 20px;
    line-height: 1.2;
    color: #ffffff;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 45px;
    transition: .2s;
    &:hover {
      color: rgb(199, 199, 199);
    }
  }
  &__link {
    line-height: 100px;
    position: relative;
    white-space: nowrap;
    &::after {
      content: '';
      display: block;
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 6px;
      background-color: #11749e;
      transition: .2s;
      transform: scaleX(0);
    }
    &:hover::after {
      transform: scaleX(1);
    }
  }
  &--active {
    padding-top: 20px;
    width: 100%;
    height: 100%;
    position: fixed;
    z-index: 2;
    left: 0;
    top: 0;
    background-color: rgba(0, 0, 0, 0.5);
    animation: showMenu .3s;
    .nav-menu__item {
      color: #f2f2f2;
      font-size: 9vh;
      margin-left: 0;
    }
    .nav-menu__link {
      line-height: 15vh;
    }
    .nav-menu__link:hover {
      color: #b3e0e4;
    }
    .nav-menu__link::after {
      display: none;
    }
  }
  @media screen and (max-width: 1024px) {
    display: none;
  }
  @media screen and (max-width: 1024px) {
    &--active {
      display: block;
      // top: 75px;
    }
  }
}

@keyframes showMenu {
  0% {
    transform: translateY(-100%);
  }
  100% {
    transform: translateY(0%);
  }
}
.socials {
  display: flex;
  justify-content: space-between;
  &__item {
    transform: scale(1.5);
    margin-left: 25px;
    transition: .2s;
    &:hover {
    transform: scale(2);
    }
  }
  &__link {

  }
  @media screen and (max-width: 450px) {
    &__item {
      margin-left: 20px;
    }
  }
}