@font-face {
  font-family: 'Gotham';
  font-display: swap;
  src: url('../fonts/Gotham-Book.otf');
  src: local('O'), url('../fonts/Gotham-Book.woff') format('woff'),
    url('../fonts/Gotham-Book.otf') format('opentype');
  font-weight: 400;
  font-style: normal;
}
@font-face {
  font-family: 'Gotham';
  font-display: swap;
  src: url('../fonts/Gotham-Medium.otf');
  src: local('O'), url('../fonts/Gotham-Medium.woff') format('woff'),
    url('../fonts/Gotham-Medium.otf') format('opentype');
  font-weight: 500;
  font-style: normal;
}
@font-face {
  font-family: 'Gotham';
  font-display: swap;
  src: url('../fonts/Gotham-Bold.otf');
  src: local('O'), url('../fonts/Gotham-Bold.woff') format('woff'),
    url('../fonts/Gotham-Bold.otf') format('opentype');
  font-weight: 700;
  font-style: normal;
}
@font-face {
  font-family: 'MyriadPro';
  font-display: swap;
  src: url('../fonts/MyriadPro-Regular.otf');
  src: local('O'), url('../fonts/MyriadPro-Regular.woff') format('woff'),
    url('../fonts/MyriadPro-Regular.otf') format('opentype');
  font-weight: 400;
  font-style: normal;
}

$gotham: 'Gotham', sans-serif;
$myriad: 'MyriadPro', sans-serif;
$roboto: 'Roboto', sans-serif;
$robotoCon: 'Roboto Condensed', sans-serif;
$opensans: 'Open Sans', sans-serif;