@charset 'utf-8';
@import 'basic/fonts';
@import 'basic/reset';
@import 'basic/basic';

/////Переменные
// $gotham: 'Gotham', sans-serif;
// $myriad: 'MyriadPro', sans-serif;
// $roboto: 'Roboto', sans-serif;
// $robotoCon: 'Roboto Condensed', sans-serif;
// $opensans: 'Open Sans', sans-serif;
$bg1: url(../img/bg/bg1.jpg) no-repeat center / cover;
$bg2: url(../img/bg/bg2.jpg) no-repeat center / cover;
$bg3: url(../img/bg/bg3.jpg) no-repeat center / cover;
////----

@import 'blocks/header';


.first-screen {
  background:$bg1;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding-bottom: 100px;
  &__content {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    height: 100%;
    flex-grow: 1;
  }
  &__text {
    text-align: center;
  }
  &__title {
    font-size: 152px;
    font-weight: 700;
    text-transform: uppercase;
    color: #f5f5f5;
  }
  &__subtitle {
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    color: #11749e;
    text-align-last: justify;
    letter-spacing: 0.85em;
    padding-left: 0.85em;
  }
  &__button {
    display: block;
    width: 65px;
    height: 65px;
    border: 1px solid #ffffff;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    animation: 1s ease infinite alternate scrollButtonMoving;
    position: relative;
    &::before {
      content: 'Scroll Down';
      white-space: nowrap;
      position: absolute;
      top: -33px;
      font-family: $opensans;
      font-size: 13px;
      color: #ffffff;
    }
    & .fa-icon {
      padding-top: 3px;
      color: #f5f5f5;
    }
  }
  @media screen and (max-width: 1280px) {
    padding-bottom: 40px;
    &__content {
      margin-top: 50px;
    }
  }
  @media screen and (max-width: 940px) {
    &__title {
      font-size: 13vw;
    }
    &__subtitle {
      font-size: 18px;
    }
  }
  @media screen and (max-width: 720px) {
    &__subtitle {
      font-size: 14px;
    }
  }
  @media screen and (max-width: 550px) {
    &__subtitle {
      text-align: center;
      text-align-last: center;
      letter-spacing: 0.7em;
    }
  }
}
@keyframes scrollButtonMoving {
  0% {
    transform: scale(1.1);
  }
  100% {
    transform: scale(0.9);
  }
}

.features {
  background-color: #11749e;
  padding-top: 50px;
  padding-bottom: 100px;
  &__header {
    margin-bottom: 150px;
    color: #ffffff;
    &::before {
      content: 'w';
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
  }
  & .feature:not(:last-child) {
    margin-right: 45px;
  }
  @media screen and (max-width: 878px) {
    &__row {
      flex-wrap: wrap;
    }
    & .feature {
      flex-basis: 48%;
      margin-bottom: 50px;
    }
    & .feature:not(:last-child) {
      margin-right: 0;
    }
  }
  @media screen and (max-width: 553px) {
    & .feature {
      flex-basis: 100%;
      margin-bottom: 50px;
    }
  }
}
.feature {
  flex-basis: 25%;
  display: flex;
  flex-direction: column;
  align-items: center;
  color: #ffffff;
  text-align: center;
  &:hover &__circle {
    transform: scale(1.05);
    animation: rotateFeatureCircle 1s normal;
  }
  &__circle {
    width: 130px;
    height: 130px;
    background-color: #2e85aa;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 25px;
  }
  &__icon {

  }
  &__title {
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 30px;
  }
  &__text {
    line-height: 24px;
    font-weight: 400;
  }
  @media screen and (max-width: 878px) {
    &__text {
      padding: 0 30px;
    }
  }
}
@keyframes rotateFeatureCircle {
  0% {
    transform: rotate(0deg) scale(1.05);
  }
  15% {
    transform: rotate(20deg) scale(1.05);
  }
  50% {
    transform: rotate(-20deg) scale(1.05);
  }
  85% {
    transform: rotate(0deg) scale(1.05);
  }
}

.about {
  &__row {
    display: flex;
    justify-content: space-between;
  }
  &__content {
    flex-basis: 50%;
    padding-left: 7%;
    padding-top: 5%;
    padding-right: 5%;
  }
  &__content-meta {
    font-size: 13px;
    margin-bottom: 15px;
  }
  &__date {
    &::after {
      content: '\2003|';
    }
  }
  &__src {
    &::before {
      content: '\2003';
    }
  }
  &__content-title {
    font-size: 25px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 35px;
  }
  &__content-text {
    line-height: 24px;
    &:not(:last-child) {
      margin-bottom: 30px;
    }
  }
  &__img-wrapper {
    flex-basis: 50%;
  }
  &__img {

  }
  @media screen and (max-width: 1200px) {
    &__img-wrapper {
      padding-top: 10%;
    }
  }
  @media screen and (max-width: 768px) {
    &__img-wrapper {
      padding-top: 0;
      order: 2;
    }
    &__row {
      flex-direction: column;
    }
    &__content {
      flex-basis: fill;
      padding-bottom: 20px;
      // order: 1;
    }
  }
}

.blog {
  &__header {
    margin-bottom: 75px;
    color: #222222;
    &::before {
      content: 'b';
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 75px;
    & .blog-post:not(:last-child) {
      margin-right: 45px;
    }
  }
  @media screen and (max-width: 1024px) {
    &__row {
      flex-wrap: wrap;
    }
    & .blog-post {
      flex-basis: 48%;
      margin-bottom: 40px;
    }
    & .blog-post:not(:last-child) {
      margin-right: 0;
    }
  }
  @media screen and (max-width: 485px) {
    & .blog-post {
      flex-basis: 100%;
    }
  }
}
.blog-post {
  transition: .3s;
  padding: 10px;
  &:hover {
    background-color: rgba(195, 205, 221, 0.5);
    box-shadow: 0 0 15px 0 rgba(195, 205, 221, 0.5);
    transform: scale(1.025);
  }
  &__img-wrapper {
    margin-bottom: 45px;
  }
  &__img {
    margin: auto;
  }
  &__title {
    margin-bottom: 25px;
    text-align: center;
    font-size: 18px;
    font-weight: 500;
    text-transform: uppercase;
    color: #11749e;
    &:hover {
      text-decoration: underline;
    }
  }
  &__link {

  }
  &__text {
    text-align: center;
    line-height: 24px;
    color: #222222;
  }
}

.notes {
  padding: 150px 0;
  background-color: #11749e;
  &__inner {
    display: flex;
    justify-content: space-between;
  }
  @media screen and (max-width: 550px) {
    // flex-basis: 100%;
    & &__inner {
      flex-direction: column;
      // margin-right: 20px;
      // margin-bottom: 10px;
      // float: left;
    }
  }
}
.note {
  flex-basis: 48%;
  display: flex;
  align-items: flex-start;
  &__icon-wrapper {
    margin-right: 35px;
  }
  &__icon {
    max-width: none;
    width: 70px;
    // float: left;
  }
  &__text {
    line-height: 24px;
    color: #ffffff;
  }
  @media screen and (max-width: 1024px) {
    display: block;
    & &__icon {
      margin-right: 20px;
      margin-bottom: 10px;
      float: left;
    }
  }
  @media screen and (max-width: 550px) {
    flex-basis: 100%;
    margin-bottom: 50px;
  }
}

.benefits {
  background:$bg2;
  padding: 125px 0 95px 0;

  &__inner {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding-right: 60px;
    padding-left: 60px;
  }
  &__content {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    // flex-basis: 690px;
    flex-basis: 59%;
    padding-right: 200px;

  }
  &__content-title {
    font-size: 25px;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
    margin-bottom: 40px;
  }
  &__content-text {
    line-height: 24px;
    color: #ffffff;
    margin-bottom: 65px;
  }
  &__content-feature {
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
    padding: 13px 0 13px 20px;
    background-color: #11749e;
    margin-bottom: 25px;
    transition: .2s;
    &--pr135 {padding-right: 135px;}
    &--pr220 {padding-right: 220px;}
    &--pr160 {padding-right: 160px;}
    &:hover {
      box-shadow: 0 0 10px 2px #11749e;
      transform: scale(1.05);
    }
  }
  &__img-wrapper {

  }
  &__img {

  }
  @media screen and (max-width: 950px) {
    &__content {
      padding-right: 100px;
    }
  }
  @media screen and(max-width: 790px) {
    &__img-wrapper {
      display: none;
    }
    &__content {
      padding-right: 0;
      flex-basis: 100%;
    }
  }
  @media screen and (max-width: 450px) {
    &__content-feature {
      padding-right: 30px;
    }
  }
}

.team {
  padding-bottom: 120px;
  transition: .2s;
  &__header {
    margin-bottom: 70px;
    &::before {
      content: 't';
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    & .team-member:not(:last-child) {
      margin-right: 30px;
    }
  }
  @media screen and (max-width: 878px) {
    &__row {
      flex-wrap: wrap;
    }
    & .team-member {
      flex-basis: 48%;
      margin-bottom: 50px;
    }
    & .team-member:not(:last-child) {
      margin-right: 0;
    }
  }
  @media screen and (max-width: 450px) {
    & .team-member {
      flex-basis: 100%;
    }
  }
}
.team-member {
  position: relative;
  transition: .3s;
  &:hover {
    transform: scale(1.05);
    box-shadow: 0 0 10px 5px rgba($color: #000000, $alpha: 0.3);
  }
  &__img-wrapper {}
  &__img {
    margin: auto;
  }
  &__info {
    text-align: center;
    background-color: #fff;
    position: absolute;
    bottom: -15px;
    left: 50%;
    transform: translateX(-50%);
    padding: 22px 40px 0 40px;
  }
  &__name {
    font-size: 20px;
    font-weight: 500;
    text-transform: uppercase;
    white-space: nowrap;
    color: #11749e;
    margin-bottom: 10px;
  }
  &__post {
    font-weight: 500;
    color: #000000;
  }
  @media screen and (max-width: 550px) {
    &__info {
      padding: 22px 10px 0 10px;
    }
  }

}

.contact {
  background:$bg3;
  padding-top: 50px;
  padding-bottom: 170px;
  &__inner {
    
  }
  &__header {
    margin-bottom: 150px;
    color: #ffffff;
    &::before {
      content: 'c';
    }
  }
  &__form {

  }
}
.contact-form {
  & input,
  & textarea {
    border: 1px solid #ffffff;
    background: none;
    padding: 20px 0 16px 22px;
    font-size: 15px;
    color: #ffffff;
    outline: none;
    transition: .2s;
    &::placeholder {
      font-size: 15px;
      color: #ffffff;
    }
    &:focus {
      box-shadow: 0 0 10px 3px rgba($color: #ffffff, $alpha: 0.6);
      background-color: rgba($color: #ffffff, $alpha: 0.1);
    }
    &:focus::placeholder {
      opacity: 0;
    }
  }
  &__row {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
    & input {
      flex-basis: 33.333%;
    }
  }
  &__name {
    margin-right: 30px;
  }
  &__email {
    margin-right: 30px;
  }
  &__tel {}
  &__message {
    resize: none;
    width: 100%;
    height: 150px;
    margin-bottom: 65px;
  }
  &__btn {
    padding: 20px 66px 26px;
    margin: auto;
    display: block;
    border: none;
    outline: none;
    background-color: #11749e;
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
    color: #ffffff;
    transition: .1s;
    &:active {
      box-shadow: inset 0 0 20px 5px rgba($color: #09455f, $alpha: 1.0);
    }
  }
  @media screen and (max-width: 768px) {
    &__row {
      flex-wrap: wrap;
    }
    &__row input {
      flex-basis: 100%;
      margin-right: 0;
      margin-bottom: 20px;
    }
  }
}

