body {
  font-family: $gotham;
  font-size: 15px;
  // line-height: 24px;
  font-weight: 400;
  &.lock {
    overflow-y: hidden;
  }
}

.dark-bg {
  background-color: #222222;
  color: #ffffff;
}
.light-bg {
  background-color: #f3f3f3;
  color: #222222;
}
.float-right {
  float: right;
}
.float-left {
  float: left;
}
.container {
  margin: auto;
  max-width: 1200px;
  width: 100%;
  padding-left: 15px;
  padding-right: 15px;
}
// .fa-icon {}
.section-header {
  text-align: center;
  position: relative;
  z-index: 10;
  padding-top: 125px;
  &::before {
    position: absolute;
    z-index: 5;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    text-transform: uppercase;
    font-family: $robotoCon;
    font-size: 246px;
    font-weight: 700;
  }
  &--light::before {
    color: rgba(255, 255, 255, 0.2);
  }
  &--dark::before {
    color: rgba(34, 34, 34, 0.2);
  }
  &__title {
    font-size: 38px;
    font-weight: 500;
    text-transform: uppercase;
    margin-bottom: 110px;
  }
  &__subtitle {
    font-size: 30px;
    color: #222222;
  }
}